import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./assets-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class AssetAddComponent  {
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;

	assetForm: FormGroup;
	status;
	assetId = 0;  
	asset = new AssetData();

	msg = [];
	bootstrapFormValues = [];
	bootstrapMsg='';
	assetacquireddatemsg:string;
	assetcustomermsg:string;
	assetpurchasedatemsg:string;

	public customer = [];
    public location = [];
  	public type = [];
  	public brand = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.assetId).subscribe(params=>{
			if(params.assetId != undefined){
				this.assetId=params.assetId; 
			}
		}); 
	
	if(this.assetId == 0){
		this.status = "Add";
		this.populateDropDownData();
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.assetForm = new FormGroup({	
			'assetContractassettypeId' : new FormControl('', [ Validators.required]),
			'assetAssetbrandId' : new FormControl('', [ Validators.required]),
			'assetContractlocId' : new FormControl('', [ Validators.required ]),
			'assetName' : new FormControl('', [ Validators.required]),
			'assetSerial' : new FormControl('', [ Validators.required]),
			'assetTag' : new FormControl(''),
			'assetDesc' : new FormControl(''),
			'assetPurchaseDate' : new FormControl(''),
			'assetNotes' : new FormControl(''),
		});
	}
	  
	  get assetContractassettypeId(){ return this.assetForm.get('assetContractassettypeId')};
	  get assetAssetbrandId(){ return this.assetForm.get('assetAssetbrandId')};
	  get assetContractlocId(){ return this.assetForm.get('assetContractlocId')};
	  get assetName(){ return this.assetForm.get('assetName')};
	  get assetSerial(){ return this.assetForm.get('assetSerial')};
	  get assetTag(){ return this.assetForm.get('assetTag')};
	  get assetDesc(){ return this.assetForm.get('assetDesc')};
	  get assetPurchaseDate(){ return this.assetForm.get('assetPurchaseDate')};
  	  get assetNotes(){ return this.assetForm.get('assetNotes')};
	 
  
	  
	   populateDropDownData(){
		  const headers = new HttpHeaders({'Content-Type':'application/json'});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/servicecontractlocation',{},
									{ headers, withCredentials: true }).subscribe(data =>{ 
				  														this.location  = data['populateServiceContractLocation']
			  															});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/type',{},
									{ headers, withCredentials: true }).subscribe(data =>{ 
				  														this.type  = data['populateType']
			  															});
		   this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/brand',{},
									{ headers, withCredentials: true }).subscribe(data =>{ 
				  														this.brand  = data['populateBrand']
			  															});
	  }
	  
  	  populateLocation(value){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<any[]>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/servicecontractlocation',
		 			JSON.stringify({ assetCustomerId : value }),
				{ headers, withCredentials: true }).subscribe(data =>{
					this.location  = data['populateServiceContractLocation']
			  															});
		  }
	  
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<AssetData>(sessionStorage.getItem('requestUrl')+'/axelacrm/service/asset-data',
							 			JSON.stringify({ assetId :this.assetId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.asset = data['populateData'][0];
				  									this.populateLocation(this.asset.assetCustomerId);
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
			  														this.populateDropDownData();						
				  
				   this.http.post<AssetData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer-populate/customer-selectto?customerId='+this.asset.assetCustomerId,JSON.stringify({ }),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.asset.assetCustomer =data['results'][0].text;
					$('#customer').html('<option value='+this.asset.assetCustomerId+'>'+this.asset.assetCustomer+'</option>');
			  															},
															 (err) => {
																	console.log("ERROR: "+err);
															 },
															 () => {
															 });
					
															 });
	 }
	  
	   ngAfterViewInit(){
		  $.getScript('assets/demo/default/custom/components/forms/widgets/select2.js',function(){
		   });
		  
		   $.getScript('assets/app/js/bootstrap-material-datetimepicker.js',function(){
		   });
		   
		   
		   	$("#customer").change(function(){
//				alert($("#customer").val());
				$('#hidden').val($("#customer").val());
						$('#hidden').focus();
						$('#hidden').blur();
			});
		   
		}	
	
	  toggleAnimation(){
		   this._router.navigate(['service/assets-list']);
		}

	  onSubmit() {
		  this.bootstrapFormValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.asset[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
				
				if(this.asset.assetCustomerId != undefined){
					if(this.asset.assetCustomerId == '0'){
						this.bootstrapMsg = this.bootstrapMsg + 'Plese Select Customer';
						this.assetcustomermsg="<font class='errorMsg'>Plese Select Customer</font>";
					}else{
						this.assetcustomermsg = "";
					}
				}else{
					this.assetcustomermsg="<font class='select2ErrorMsg'>Select Customer</font>";
				}
				
				if(this.bootstrapFormValues[i].name=="assetPurchaseDate"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Start Date!<br>';
						this.assetpurchasedatemsg="<font class='errorMsg'>Select Date</font>";
						
					}else{
						this.assetpurchasedatemsg = "";
					}
				}
			
			}
		if(this.bootstrapMsg != ''){
				this.msg[0]='Error';
			 	this.msg[1]='Enter the Mandatory Fields' ;
				this.toast.showtoast(this.msg);
				this.bootstrapMsg='';
			}
		  if(!this.assetForm.invalid && this.bootstrapMsg == ''){
			  const headers = new HttpHeaders({'Content-Type':'application/json'});
		if(this.assetId == 0){
			this.asset['requestType'] = 'add';
		}else{
			this.asset['requestType'] = 'update';
			this.asset['assetId'] = this.assetId+'';
		}
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/asset',
									JSON.stringify(this.asset),
						{ headers, withCredentials: true }).subscribe(data => {
			 											
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['errormsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.assetForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  }
	  }
	  
	  onDelete(){
		 this.asset['requestType'] = 'delete';
		 this.asset['assetId'] = this.assetId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/asset',
									JSON.stringify({ requestType : 'delete',
												   'assetId' : this.assetId }),
						{ headers, withCredentials: true }).subscribe(data => {
			 											
			 											if(data['successmsg'].includes('Successfully')){
														this.msg[0]='success';
			 											this.msg[1]=data['successmsg'];
														}else{
														this.msg[0]='Error';
			 											this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.assetForm.reset();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
														});
		 
	 }  
}
	
export class AssetData {
	assetId: string;
	assetCustomerId: string;
	assetCustomer:string;
	assetContractassettypeId: string;
	assetAssetbrandId: string;
	assetContractlocId: string;
	assetName: string;
	assetSerial: string;
	assetTag: string;
	assetDesc: string;
	assetPurchaseDate: string='';
	assetNotes: string;
	assetEntryId: string;
	assetEntryDate: string;
	assetModifiedId: string;
	assetModifiedDate: string;
	contactId:string;
}



